/* ******* Tags ******* */

* {
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

html {
  height: 100%;
  overflow-x: hidden;
  position:relative;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, main, nav, section, summary { display: block; }

body {
  font-family: $CmFont-Base-Family;
  font-size: $CmFont-Base-Size;
  line-height: $CmFont-Base-Height;
  font-weight: $CmFont-Base-Weight;
  color: $CmFont-Base-Color;
  background-color: #fff;
  margin: 0;
  padding: 0;
  cursor: default;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: subpixel-antialiased;
}

::selection {
  background: $CmColor-Primary-Default;
  color: $CmColor-Font-Opposite;
  opacity: 1;
}

::-moz-selection {
  background: $CmColor-Primary-Default;
  color: $CmColor-Font-Opposite;
  opacity: 1;
}

::-webkit-selection {
  background: $CmColor-Primary-Default;
  color: $CmColor-Font-Opposite;
  opacity: 1;
}

img {
  border: none;
  outline: none;
  transform: translate(0px, 0px);
}

svg {
  width: 100%;
  height: 100%;
}

figure {
  overflow: hidden;
  margin: 0;
  padding: 0;
}

dl, dt, dd, canvas, iframe, form {
  padding: 0;
  margin: 0;
  display: block;
  text-indent: 0;
}

iframe {
  width: 100%;
  display: block;
  border: none;
}

header, main, aside, footer, section { display: block; }

b { font-weight: bold; }

hr {
  height: 0;
  margin: 24px 0;
  padding: 0;
  display: block;
  overflow: hidden;
  border: none;
  clear: both;
}

a {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

a:hover,
a:focus {
  text-decoration: underline;
  outline: 0 none;
}

select,
input,
button,
textarea {
  outline: none !important;
  margin:0;
}

input,
textarea {
  resize:none;
  appearance: none;
  border-radius:0;
}

input { width: 100%; }

.clear {
  clear:both;
  height:0;
  font-size:0;
}

ul {
  padding:0;
  margin:0;
}

li {
  padding:0;
  margin:0;
  list-style:none;
}

h1,h2,h3,h4,h5,h6 {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

p:last-child { margin-bottom: 0; }

/* ******* LIST ******* */

.g__list { list-style: disc;

  &.has-indent {
    ul, li { margin: 8px 0 0 0;

      &:first-child { margin: 0;  }

    }

  }

  ul { list-style: circle;

    ul { list-style: disc; }

  }

  &-inline { text-align: left;

    > li {
      display: inline-block;
      vertical-align: top;
    }

  }

  &-clear {
    margin: 0;
    padding: 0;
    list-style: none;

    > li {
      margin: 0;
      padding: 0;
    }

  }

}

/* ******* TYPOGRAPHY ******* */

.title-h1 {
  font-family: $Font-H1-Family;
  line-height: $Font-H1-LineHeight;
  font-size: $Font-H1-Size;
  font-weight: $Font-H1-Weight;
  color: $Font-H1-Color;
  font-style: $Font-H1-Style;

  @include breakpoint(1600px) { font-size: 55px; }

  @include breakpoint(1440px) { font-size: 40px; }

  @include breakpoint(1280px) { font-size: 35px; }

  @include breakpoint(1024px) { font-size: 30px; }

}

.title-h2 {
  font-family: $Font-H2-Family;
  line-height: $Font-H2-LineHeight;
  font-size: $Font-H2-Size;
