.text{
  font-family: $Font-P-Family;
  line-height: $Font-P-LineHeight;
  font-size: $Font-P-Size;
  font-weight: $Font-P-Weight;
  color: $Font-P-Color;
  font-style: $Font-P-Style;
}

a {
  font-weight: $Font-A-Weight;
  color: $Font-A-Color;
  text-decoration: none;
  font-style: $Font-A-Style;

  &:hover{ text-decoration: none; color: $CmColor-Primary-Default; }

}

._align-center{ text-align: center; }

._align-left{ text-align: left; }

._align-right{ text-align: right; }

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}