.restaurants-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 80px;

  @include breakpoint(1280px){  margin-bottom: 60px; }

  .item {
    position: relative;
    width:  (100/7*1%);
    padding-bottom: 30px;
    box-sizing: border-box;
    text-align: center;
    transition: 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);

    @include breakpoint(1280px){ width: (100/4*1%); }

    @include breakpoint(768px){ width: (100/3*1%); }

    @include breakpoint(640px){ width: (100/2*1%); }

    @include breakpoint(480px){ width: (100*1%); }

    &:hover {
      box-shadow: 0 0 40px 0 rgba(101, 37, 51, 0.4);
      transition-delay: 150ms;
    }

  }

  .image-wrap {
    width: 110px;
    margin: 0 auto 20px;
    background-color: #fff;
  }

  img { width: 100%; height: auto; }

  .rating-wrap {
    position: relative;
    width: 140px;
    height: 6px;
    margin: 0 auto 16px;

    .rating {
      position: relative;
      height: 100%;
      border-radius: 3px;
      z-index: 2;
    }

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      z-index: 1;
      height: 100%;
      background-color: #fff;
      border-radius: 3px;
    }

  }

  .reviews {
    font-size: 12px;
    font-weight: 800;
    text-align: center;
  }

}