.btn {
  position: relative;
  padding: 0 60px;
  border-radius: 27px;
  height: 54px;
  line-height: 48px;
  border: 3px solid #ededed;
  box-sizing: border-box;
  font-size: 15px;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  background-color: #fff;
  cursor: pointer;
  transition: all 300ms ease-in-out; }
  .btn span {
    transition: 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    display: inline-block; }
  .btn:after {
    content: '';
    position: absolute;
    width: 24px;
    height: 24px;
    right: 24px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    opacity: 0;
    transform: translateX(100%);
    background: url(../images/icons/button-arrow-icon-black.svg) center no-repeat;
    background-size: contain;
    transition: 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955); }
  .btn:hover span {
    transform: translateX(-12px); }
  .btn:hover:after {
    opacity: 1;
    transform: none; }
  .btn._bg, .btn._action {
    position: relative;
    color: #fff;
    background-color: #652533;
    border: 3px solid #652533;
    transition: all 300ms ease-in-out; }
    .btn._bg:before, .btn._action:before {
      content: '';
      position: absolute;
      z-index: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 74%;
      height: 30px;
      bottom: 36px;
      box-shadow: 0 60px 40px 0 #652533;
      opacity: 1;
      transition: all 300ms ease-in-out; }
    .btn._bg:after, .btn._action:after {
      content: '';
      position: absolute;
      width: 24px;
      height: 24px;
      right: 24px;
      top: 0;
      bottom: 0;
      margin: auto 0;
      opacity: 0;
      transform: translateX(100%);
      background: url(../images/icons/button-arrow-icon.svg) center no-repeat;
      background-size: contain;
      transition: 0.3s cubic-bezier(0.455, 0.03, 0.515, 0.955); }
    .btn._bg:hover span, .btn._action:hover span {
      transform: translateX(-12px); }
    .btn._bg:hover:after, .btn._action:hover:after {
      opacity: 1;
      transform: none; }

