// /* ******* COMMON ******* */

@mixin mx__transition($properties: all, $duration: $CmUI-Transition-Duration, $function: ease-in-out) {
  transition: $properties $duration $function;
}

@mixin mx__position ($top: auto, $right: auto, $bottom: auto, $left: auto) {
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

/* ******* INLINE STYLES ******* */

.clearfix,
.clear {

  &:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    font-size: 0;
    height: 0;
  }

}

.marginfix {

  &:before {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    font-size: 0;
    height: 0;
  }

}

.display-none { display: none !important; }

.display-inline,
.inline { display: inline-block; }

.margin-center { margin: 0 auto; }

.margin-none { margin: 0 !important; }

.block-flex-left { justify-content: flex-start; }

.block-flex-right { justify-content: flex-end; }

.block-flex-center { justify-content: center; }

.float-none { float: none !important; }

.align-left { text-align: left !important; }

.align-center { text-align: center !important; }

.align-right { text-align: right !important; }

.valign-top { vertical-align: top !important; }

.valign-middle { vertical-align: middle !important; }

.valign-bottom { vertical-align: bottom !important; }

.link--inherit {
  color: inherit;
  cursor: pointer;
  text-decoration: none;

  &:hover { text-decoration: underline; }

}

// /* *** TEXT *** */

.pull-center { text-align: center; }

.pull-left { text-align: left; }

.pull-right { text-align: right; }

.mx__text-reductions {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// /* *** IMAGES *** */

@mixin mx__object-fit($value: fill) {
    object-fit:$value;
}

@mixin mx__object-position($value: center) {
    object-position: $value;
}

@mixin mx__image-rendering($value: auto) {
  image-orientation: $value;
}

@mixin mx__image-orientation($value: 0deg) {
  image-orientation: $value;
}


@mixin mx__transition($properties: all, $duration: $CmUI-Transition-Duration, $function: ease-in-out) {
  transition: $properties $duration $function;
}

.mx__center-xy {
  position: absolute;
  transform: translate(-50%, -50%);
  @include mx__position (50%, auto, auto, 50%);
}

// /* ******* IMAGES ******* */

.ex__image-resizable {
  width: 100%;
  height: auto;
  display: block;
}
.ex__image-flexible {
  width: 100%;
  height: 100%;
  display: block;
}
.ex__image-adaptive {
  width: auto;
  max-width: 100%;
  height: auto;
  display: block;
}