.g-container {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-size: auto;
  overflow: hidden;

  &.is-header-fixed { margin-top:70px;

    .g-header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 302;
    }

  }

}


.g-header,
.g-content,
.g-footer {  position: relative; }

.g-header {
  height: 70px;
  font-size: 13px;
  font-weight: 400;

}

.g-content { flex: 1 1 auto; }

.g-footer {

  &.is-fixed { position: fixed; }

}

.inner {
  position: relative;
  margin: 0 auto;
  clear: both;

  @include minBreakpoint(1919px){ max-width: 1800px; }

  @include breakpoint(1800px){
    max-width: 100%;
    padding-left: 70px;
    padding-right: 70px;
  }

  @include breakpoint(1024){
    padding-left: 35px;
    padding-right: 35px;
  }

  @include breakpoint(700px){
    padding-left: 23px;
    padding-right: 23px;
  }

  &.full-width { max-width: 100%; }

  &.width-1 { max-width: 1920px;

    @include breakpoint(1800px){ max-width: 100%; }

  }

  &.width-2 { max-width: 1428px;

    @include breakpoint(1280px){ max-width: 100%; }

  }

  &.full-height {
    height: 100%;
    padding-top: 0
  }

  &.inner-content {
    -webkit-box-sizing: content-box;
    box-sizing: content-box
  }

}

.block { position: relative; }