// /* ******* COLORS ******* */
// /* ************************************************ */

// /* ******* PRIMARY ******* */

$CmColor-Primary-Default: #652533;
$CmColor-Primary-Hover: darken($CmColor-Primary-Default, 10%);
$CmColor-Primary-Active: darken($CmColor-Primary-Default, 15%);
$CmColor-Primary-ActiveHover:darken($CmColor-Primary-Default, 10%);

