// /* ******* FONT ******* */

$CmColor-Font: #666666;
$CmColor-Font-Opposite: #ffffff;
$CmColor-Font-Hint: #999999;
$CmColor-Font-Placeholder: #b7b7b7;
$CmColor-Font-Link: $CmColor-Primary-Default;
$CmColor-Font-LinkHover: $CmColor-Primary-Hover;
$CmColor-Font-LinkActive: $CmColor-Primary-Active;

$CmUI-Transition-Duration: 300ms;
$CmUI-Transition-DurationReverse: 100ms;
$CmUI-Transition-Duration-Section: 1s;

