.g-container {  background-color: #fff;

  &._bg-home {
    position: relative;
    min-height: 100vh;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: -18%;
      bottom: 0;
      left: -18%;
      background-repeat: no-repeat;
      background-image: url(../images/bg.jpg);
      background-position: center top;

      @include breakpoint(1920px) {  background-size: contain; }

      @include breakpoint(1280px) { opacity: 0.6; }

      @include breakpoint(768) { display: none; }

    }

  }

}