.food-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-top: 100px;
  margin-bottom: -30px;

  @include breakpoint(1280px){
    margin-top: 60px;
    margin-bottom: -45px;
  }

  @include breakpoint(768px){ margin-bottom: -30px; }

  @include breakpoint(640px){ margin-bottom: -24px; }

  .item {
    display: flex;
    position: relative;
    flex-direction: column;
    align-content: center;
    width:  (100/6*1%);
    cursor: pointer;
    padding-bottom: 30px;
    box-sizing: border-box;

    @include breakpoint(1280px){
      width:  (100/3*1%);
      padding-bottom: 45px;
    }

    @include breakpoint(640px){
      width: (100/2*1%);
      padding-bottom: 30px;
    }

    @include breakpoint(480px){
      width:  (100*1%);
      padding-bottom: 24px;
    }

    &:hover {

      //.image-wrap {
      //
      //  &:before {
      //    opacity: 1;
      //    transform: scale(1, 1);
      //  }
      //
      //}

      .title { color: #652533; }

      .desc { opacity: 1; }

    }

  }

  .image-wrap {
    position: relative;
    margin-bottom: 35px;
    text-align: center;

    //&:before {
    //  content: '';
    //  position: absolute;
    //  top: 50%;
    //  left: 50%;
    //  margin: -56px 0 0 -56px;
    //  transform: scale(0.6, 0.6);
    //  z-index: 0;
    //  height: 112px;
    //  width: 112px;
    //  border-radius: 50%;
    //  background-color: #652533;
    //  opacity: 0;
    //  @include mx__transition(all, $CmUI-Transition-Duration);
    //}

  }

  .content-wrap { position: relative; }

  .title {
    font-size: 15px;
    font-weight: bold;
    text-align: center;
    @include mx__transition(all, $CmUI-Transition-Duration);
  }

  .desc {
    opacity: 0;
    position: absolute;
    left: 50%;
    bottom: -34px;
    transform: translateX(-50%);
    width: 100%;
    text-align: center;
    font-size: 13px;
    line-height: 1.8;
    color: #808080;
    @include mx__transition(all, $CmUI-Transition-Duration);


    @include breakpoint(768px){ bottom: -22px; }

  }

}