.news-wrap {

  .item-wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -32px -32px;

    @include breakpoint(1280px){ margin: 0 -16px -32px; }

  }

  .item {
    padding: 0 32px 32px;
    box-sizing: border-box;
    width: (100/3*1%);

    @include breakpoint(1280px){ padding: 0 16px 32px; }

    @include breakpoint(1024px){ width: (100/2*1%); }

    @include breakpoint(768px){ width: (100*1%); }

    .image-wrap { position: relative;

      a { display: block; }

      &:after,
      &:before {
        content: '';
        position: absolute;
        opacity: 0;
        transition: .3s cubic-bezier(.455,.03,.515,.955);
      }

      &:before {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border-radius: 4px;
        background: rgba(0,0,0,.5);
        transition-delay: 150ms;
        z-index: 2;
      }

      &:after {
        width: 34px;
        height: 34px;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        z-index: 3;
        transform: translateX(10px);
        background: url(../images/icons/button-arrow-icon.svg) center no-repeat;
        background-size: contain;
      }

      &:hover {

        &:after,
        &:before {
          opacity: 1;
          transition-delay: 150ms;
        }

        &:after {
          transform: none;
        }

      }

    }

  }

  .image-wrap { margin-bottom: 40px; @include mx__transition(all, $CmUI-Transition-Duration);

    @include breakpoint(1024px){ margin-bottom: 30px; }

    @include breakpoint(768px){ margin-bottom: 24px; }

    @include breakpoint(640px){ margin-bottom: 16px; }

    img { width: 100%; display: block;}

  }

  .title {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.5;
    transition: color .3s cubic-bezier(.455,.03,.515,.955);

    &:hover {color: #652533; }

  }

  .desc {
    font-size: 13px;
    line-height: 1.77;
    color: #8c8c8c;
  }

  .data {
    margin-top: 15px;
    font-size: 11px;
    line-height: 2.3;
    color: #8c8c8c;

    @include breakpoint(768px){ margin-top: 0; }

  }

}