.g-footer { background-color: #986d76;

  .footer-content {
    min-height: 50px;
    padding: 18px 0;
    box-sizing: border-box;
  }

  .footer-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    font-size: 13px;

    @include breakpoint(480px) {
      flex-direction: column;
      align-items: start;
    }

  }

  .footer-copyright { line-height: 1.2;

    @include breakpoint(1445px) { flex: 1 1 50%; }

  }

  .footer-working { text-align: right; line-height: 1.2;

    @include breakpoint(1445px) { flex: 1 1 50%; }

    @include breakpoint(480px) { margin-top: 16px; }

    a {
      margin-left: 3px;
      color: #fff;
      position: relative;

      &:after {
        content: '';
        opacity: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        background: #fff;
        transform: translateY(10px);
        transition: transform .3s,opacity .3s,-webkit-transform .3s;

      }

      &:hover {

        &:after {
          opacity: 1;
          transform: translateY(0);}

      }

    }

  }

  .footer-menu {

    @include breakpoint(1445px) {
      order: -1;
      flex: 1 1 100%;
      margin-bottom: 18px;
    }

    .menu-wrap {

      ul {

        @include breakpoint(960px) { margin-bottom: -16px; }

        li {

          @include breakpoint(1560px) { margin-right: 16px; }

          @include breakpoint(960px) { margin-right: 10px; }

          @include breakpoint(850px) {
            padding-bottom: 16px;
            margin-right: 0;
            width: (100/3*1%);
          }

          @include breakpoint(480px) { width: 100%; }

          a { position: relative;

            &:after {
              content: '';
              opacity: 0;
              position: absolute;
              bottom: 0;
              left: 0;
              right: 0;
              height: 1px;
              background: #fff;
              transform: translateY(10px);
              transition: transform .3s,opacity .3s,-webkit-transform .3s;

            }

          }

          &:hover {

            a {

              &:after {
                opacity: 1;
                transform: translateY(4px);
              }

            }

          }

        }

      }

    }

  }

}