$common_space_directions: bottom top;

@each $direction in $common_space_directions {
  [class*="_cs-xl-#{$direction}"] { padding-#{$direction}: 100px;

  }

  [class*="_cs-l-#{$direction}"] { padding-#{$direction}: 80px;

  }

  [class*="_cs-m-#{$direction}"] { padding-#{$direction}: 60px;

  }

  [class*="_cs-ms-#{$direction}"] { padding-#{$direction}: 30px;

  }

  [class*="_cs-s-#{$direction}"] { padding-#{$direction}: 20px;

  }
}