.button-wrap {

  .btn { display: inline-block;

    @include breakpoint(530px){
      display: flex;
      flex-direction: column;
    }

    &+.btn { margin-left: 40px;

      @include breakpoint(530px){
        margin-top: 24px;
        margin-left: 0;
      }

    }

  }

}

.btn {
  position: relative;
  padding: 0 60px;
  border-radius: 27px;
  height: 54px;
  line-height: 48px;
  border: 3px solid #ededed;
  box-sizing: border-box;
  font-size: 15px;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  background-color: #fff;
  cursor: pointer;
  @include mx__transition(all, $CmUI-Transition-Duration);

  span { transition: .3s cubic-bezier(.455,.03,.515,.955); display: inline-block;}

  &:after {
    content: '';
    position: absolute;
    width: 24px;
    height: 24px;
    right: 24px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    opacity: 0;
    transform: translateX(100%);
    background: url(../images/icons/button-arrow-icon-black.svg) center no-repeat;
    background-size: contain;
    transition: .3s cubic-bezier(.455,.03,.515,.955);
  }

  &:hover {
    span { transform: translateX(-12px); }

    &:after {
      opacity: 1;
      transform: none;
    }

  }

  &._bg,
  &._action {
    position: relative;
    color: #fff;
    background-color: #652533;
    border: 3px solid #652533;
    @include mx__transition(all, $CmUI-Transition-Duration);

    &:before {
      content: '';
      position: absolute;
      z-index: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 74%;
      height: 30px;
      bottom: 36px;
      box-shadow: 0 60px 40px 0 #652533;
      opacity: 1;
      @include mx__transition(all, $CmUI-Transition-Duration);
    }

    &:after {
      content: '';
      position: absolute;
      width: 24px;
      height: 24px;
      right: 24px;
      top: 0;
      bottom: 0;
      margin: auto 0;
      opacity: 0;
      transform: translateX(100%);
      background: url(../images/icons/button-arrow-icon.svg) center no-repeat;
      background-size: contain;
      transition: .3s cubic-bezier(.455,.03,.515,.955);
    }

    &:hover {
      span { transform: translateX(-12px); }

      &:after {
        opacity: 1;
        transform: none;
      }

    }

  }

}