.carousel-wrap {

  .owl-item { padding-bottom: 54px; padding-top: 54px;

    @include breakpoint(640px) { padding-bottom: 34px; padding-top: 34px; }

  }

  .owl-dots { text-align: center;

    .owl-dot { display: inline-block;
      span {
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #dddddd;
      }

      &.active {

        span { background-color: #652533; }

      }

      &+.owl-dot { margin-left: 20px; }

    }

  }

  .item {
    position: relative;
    display: flex;
    border-radius: 4px;
    background-color: #fff;

    &:after,
    &:before{
      content: '';
      position: absolute;
      z-index: -1;
      left: 50%;
      transform: translateX(-50%);
      width: 92%;
      height: 30px;
    }

    &:after {
      box-shadow: 0 60px 40px 0 #652533;
      bottom: 65px;
    }

    &:before{
      box-shadow: 0 -60px 40px 0 #652533;
      top: 75px;
    }

    @include breakpoint(540px) { flex-direction: column;  }

  }

  .image-wrap {

    img {
      width: 260px;
      object-fit: cover;
      height: 100%;
      border-radius: 4px 0 0 4px;

      @include breakpoint(540px) {
        border-radius: 4px 4px 0 0;
        width: 100%;
        object-fit: contain;
        height: auto;
      }

    }

    @include breakpoint(540px) { margin-bottom: 16px; }

  }

  .content-wrap {
    position: relative;
    padding: 24px;
    background-color: #fff;

    @supports (display: flex) {
      display: flex;
      flex-direction: column;
    }

  }

  .title {
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: bold;
    line-height: 1;
  }

  .desc {
    flex: 1 0 auto;
    font-size: 15px;
    line-height: 1.54;
    color: #737373;

  }

  .link {
    padding-top: 16px;
    text-align: right;

    a {
      font-size: 15px;
      font-weight: bold;
    }

  }

}