.g-header { height: 70px; background-color: #fff; position: relative;

  @include breakpoint(480px) { height: 48px; }

  .header-content { box-shadow: 0 4px 5px 0 rgba(101,37,51,0.37); }

  .header-wrap { display: flex;

    @include breakpoint(1800px) {  padding-right: 0; }

  }

  .logo-wrap {
    width: 172px;
    height: auto;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 60px;
    margin-right: 10%;
    box-sizing: border-box;
    vertical-align: center;

    @include breakpoint(1800px) { margin-left: 0; }

    @include breakpoint(1600px) { margin-right: 4%; }

    @include breakpoint(480px) {
      width: 150px;
      height: 48px;
      padding-top: 3px;
      padding-bottom: 0;
    }

  }

  .header-search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 150px;
    padding-top: 7px;
    padding-bottom: 7px;
    flex: 1 1 auto;

    @include breakpoint(1800px) { margin-right: 8%; }

    @include breakpoint(1600px) { margin-right: 4%; }

    &.active-search {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: 0;
      padding: 14px;
      background-color: #fff;

      @include breakpoint(480px) { padding: 11px 14px; }

      .search { display: block; }

      input { height: 42px;

        @include breakpoint(480px) { height: 26px; }

      }

    }

  }

  .button-wrap { display: flex; }

  .header-button-search {
    display: none;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;

    @include breakpoint(960px) { display: flex; }

    @include breakpoint(480px) {
      width: 48px;
      height: 48px;
    }

    &:hover {
      @include mx__transition(all, $CmUI-Transition-Duration);
    }

    .icon-wrap {
      width: 22px;
      fill: #000;
    }

  }

  .header-button-phone {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    background-color: #986d76;

    &:hover {
      background-color: rgba(#652533,0.8);
      @include mx__transition(all, $CmUI-Transition-Duration);
    }

    @include breakpoint(480px) {
      width: 48px;
      height: 48px;
    }

    .icon-wrap {
      width: 22px;
      fill: #fff;
    }

  }

  .header-shopping-cart {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 70px;
    font-size: 13px;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    background-color: #652533;
    @include mx__transition(all, $CmUI-Transition-Duration);

    &:hover {  background-color: rgba(#986d76,0.8); }

    @include breakpoint(960px) { width: 70px; }

    @include breakpoint(480px) {
      width: 48px;
      height: 48px;
    }

    .icon-wrap {
      width: 22px;
      margin-right: 8px;
      fill: #fff;
    }

    .button-text {

      @include breakpoint(960px) { display: none; }

    }

  }

}