.search {
  display: flex;
  position: relative;
  width: 100%;

  @include breakpoint(960px) { display: none; }

  .input-wrap {
    position: relative;
    flex: 1 1 auto;

    input {
      height: 32px;
      border: none;
      padding-right: 25px;
      box-sizing: border-box;
      font-size: 16px;
      font-weight: bold;
      font-family: 'Raleway', sans-serif;

      &:focus::-webkit-input-placeholder {color: transparent; @include mx__transition(all, $CmUI-Transition-Duration); }

      &:focus::-moz-placeholder          {color: transparent; @include mx__transition(all, $CmUI-Transition-Duration); }

      &:focus:-moz-placeholder           {color: transparent; @include mx__transition(all, $CmUI-Transition-Duration); }

      &:focus:-ms-input-placeholder      {color: transparent; @include mx__transition(all, $CmUI-Transition-Duration); }

    }

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 1px;
      background-color: #000;

    }

  }

  .icon-wrap {
    position: absolute;
    right: 0;
    top: 50%;
    width: 22px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transform: translateY(-50%);
    display: inline-block;
    fill: #a1a1a1;
  }

}